@import '../common/variables.scss';
.offer {
  background-color: #fff;
  display: flex;
  justify-content: center;
}

.offer__wrapper {
  width: 87%;
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 10% 0;

  p {
    font-size: 24px;
    color: rgb(40, 54, 69);
    line-height: 1.2;  
  }

  br {
    display: none;
  }
}

@media (min-width: $tablet-width) {
  .offer__wrapper {
    padding: 5% 0;
    width: 94%;

    p {
    font-size: 40px;
    }

    p:first-child {
      margin-bottom: 0;
    }

    br {
      display: block;
    }
  }
}

@media (min-width: $desktop-width) {
  .offer__wrapper {
    width: 80%;

    p {
      font-size: 50px;
      line-height: 1.2;
    }

  }
}